<template>
  <div class="repos">
    <h1>Explora mis repositorios</h1>
    <div class="spinner" v-show="!repositories">
      <div class="cube1"></div>
      <div class="cube2"></div>
    </div>
    <github-card
      v-for="(repo, index) in repositories"
      :key="index"
      :name="repo.name"
      :url="repo.url"
      :description="repo.description"
      :language="repo.language"
    >
    </github-card>
  </div>
</template>

<script>
import axios from "axios";

import githubConfig from "../config/github-config.json";
import GithubCard from "@/components/GithubCard.vue";

export default {
  name: "Repos",
  components: {
    GithubCard
  },
  mounted() {
    this.getRepositories();
  },
  data() {
    return {
      repositories: null
    };
  },
  methods: {
    async getRepositories() {
      try {
        const response = await axios.get(
          "http://api.github.com/users/orlmonteverde/repos",
          {
            params: githubConfig
          }
        );
        let repositories = response.data.filter(repo => !repo.fork);
        this.repositories = this.setRepositories(repositories);
      } catch (err) {
        console.error(err);
      }
    },
    setRepositories(repos) {
      let repositories = [];
      repos.forEach(repo => {
        repositories.push({
          name: repo.name,
          url: repo.url_html,
          description: repo.description,
          updated: repo.updated_at,
          language: repo.language
        });
      });
      return repositories;
    }
  }
};
</script>

<style lang="scss">
.spinner {
  margin: 100px auto;
  width: 40px;
  height: 40px;
  position: relative;
}

.cube1,
.cube2 {
  background-color: #333;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  left: 0;

  animation: sk-cubemove 1.8s infinite ease-in-out;
}

.cube2 {
  animation-delay: -0.9s;
}

@keyframes sk-cubemove {
  25% {
    transform: translateX(42px) rotate(-90deg) scale(0.5);
  }
  50% {
    transform: translateX(42px) translateY(42px) rotate(-179deg);
  }
  50.1% {
    transform: translateX(42px) translateY(42px) rotate(-180deg);
  }
  75% {
    transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
  }
  100% {
    transform: rotate(-360deg);
  }
}
</style>
